import React, {Component} from "react"

export default class Changed extends Component {
    render() {
        const changedState = this.props.changed;
        const inputArr = changedState.map(changed => {
            return `${changed.name}: ${changed.changed}`
        })
        return (
            <>
                <input name="source" type="hidden" value="Опрос в группе ВКонтакте"/>
                <input type="hidden" value={inputArr} name='comments'/>
                <input type="hidden" value={this.props.number} name='comments'/>
            </>
        )
    }
}