import React, {Component} from "react"
import './question.sass'
import QuestionItem from "./questionItem/questionItem";

export default class Question extends Component {

    render ()   {
        let questionsArray = this.props.questionState.questions.map(question => {
            const setChange = (questionAnswer) => {
                this.props.setChange(question.questionTitle, questionAnswer)
            }
            return (<span className="question__wrapper">
                        <span className="question__wrapper-title">{question.questionTitle}</span>
                        <QuestionItem setChange={setChange.bind(this)} questionChanged={question.changed} questionAnswers={question.questionAnswers}/>
                    </span>)
        })
        return (
            <div className="wrapper">
                {questionsArray}
            </div>
            )

    }
}