import React, {Component} from 'react';
import Question from "./components/question/question";
import './bootstrap-grid.min.css'
import './App.sass'
import Changed from "./components/changed/changed";
import {HashRouter} from "react-router-dom";
export default class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            questions: [
                {
                    name: "Форма",
                    questionTitle: "Какой формы будет ваша кухня?",
                    questionAnswers: ["Угловая", "Прямая", "С островом", "П-образная"],
                    changed: "Угловая"
                },
                {
                    name: "Материал",
                    questionTitle: "Выберите материал кухни:",
                    questionAnswers: ["ЛДСП", "МДФ", "Пластик", "Нужна консультация"],
                    changed: "ЛДСП"
                },
                {
                    name: "Фурнитура",
                    questionTitle: "Выберите тип фурнитуры:",
                    questionAnswers: ["Эконом", "Стандарт", "Премиум", "Нужна консультация"],
                    changed: "Эконом"
                },
                {
                    name: "Техника",
                    questionTitle: "Бытовая техника:",
                    questionAnswers: ["Встроенная", "Отдельностоящая", "Нужна консультация"],
                    changed: "Встроенная"
                },
                {
                    name: "Покупка",
                    questionTitle: "Когда планируете покупку:",
                    questionAnswers: ["В этом месяце", "В течении 3-х месяцев", "В течении 6-ти месяцев", "Просто хочу узнать цену"],
                    changed: "В этом месяце"
                },
                {
                    name: "Оплата",
                    questionTitle: "Способы оплаты:",
                    questionAnswers: ["Наличный расчёт", "Рассрочка без переплаты 0%"],
                    changed: "Наличный расчёт"
                },
                {
                    name: "Отправка",
                    questionTitle: "Куда отправить вам расчет:",
                    questionAnswers: ["SMS", "WhatsApp", "Telegram", "Viber", "Звонок на телефон"],
                    changed: "SMS"
                }
            ],
            text: ""
        }
    }

    setChange(questionTitle, questionAnswer) {
        let newArr = this.state.questions.map(question => {
            if (question.questionTitle === questionTitle) {
                return {
                    questionTitle: question.questionTitle,
                    questionAnswers: question.questionAnswers,
                    changed: questionAnswer
                }
            } else {
                return {
                    questionTitle: question.questionTitle,
                    questionAnswers: question.questionAnswers,
                    changed: question.changed
                }
            }
        })
        this.setState(() => {
           return {
               questions: newArr
           }
        })
    }
    onInputTextChange(e) {
        this.setState({
            text: e.target.value
        });
    }

    render ()   {
        window.state = this.state
        let changedState = this.state.questions.map(changed => {
            return {changed: changed.changed, name: changed.name}
        })

        return (
            <HashRouter>
               <div className="container">
                   <span className="title">Узнайте стоимость кухни своей мечты</span>
                   <Question setChange={this.setChange.bind(this)} questionState={this.state}/>
                   <div id="number__wrapper">
                       <span>Введите номер:</span>
                       <input onChange={this.onInputTextChange.bind(this)} type="text" value={this.state.text} id="number"/>
                   </div>
                   <form action="send.php" method="POST">
                       <button type="submit" className="btn">Отправить заявку!</button>
                       <Changed number={this.state.text} changed={changedState}/>
                   </form>
               </div>
            </HashRouter>
        );
    }

}

