import React, {Component} from "react"
import './questionItem.sass'

export default class QuestionItem extends Component {

    render ()   {

        let questionsAnswersArray = this.props.questionAnswers.map(questionAnswer => {
            const change = () => {
                if (this.props.questionChanged === questionAnswer) {
                    return "active"
                } else {
                    return ""
                }
            }

            const setChange = () => {
                this.props.setChange(questionAnswer)
            }

            return (<div className={`question__wrapper-answers_item`} onClick={setChange.bind(this)}>
                        <div className={`question__wrapper-answers_item-checkbox  ${change()}`}/>
                        <span>{questionAnswer}</span>
                   </div>)

        })
        return (
            <>
                <div className="question__wrapper-answers">
                    {questionsAnswersArray}
                </div>
            </>
        )
    }
}